
.nav{
    position: fixed;
    width:100%;
    height: 96px;
    padding-left: 3%;
    -webkit-transition: top 0.3s;
    -o-transition: top 0.3s;
    transition: top 0.3s;
    z-index: 1;
    

}

.nav-item{
    text-decoration: none;
    font-weight: 100;
    color: white;
    text-align: center;
    vertical-align: middle;
    display: inline-block;
    margin-left: 5%;
}
.nav img{
    vertical-align: middle;
    margin-right: 23%;
}


